.scroll-bar {
    //
    &::-webkit-scrollbar {
        height: 0.5rem;
    }

    //
    &::-webkit-scrollbar-track {
      background-color: lighten($secondary, 20%);
      border-radius: 0.25rem;
    }

    //
    &::-webkit-scrollbar-thumb {
      background-color: darken($secondary, 15%);
      opacity: 90%;
      border-radius: 0.25rem;
    }

    //hover 
    &::-webkit-scrollbar-thumb:hover {
        opacity: 100%;
    }
}